import React, { useEffect, useState } from "react";
import { Splide, SplideSlide } from '@splidejs/react-splide';
// Default theme
import '@splidejs/react-splide/css';

// or other themes
import '@splidejs/react-splide/css/skyblue';
import '@splidejs/react-splide/css/sea-green';

// or only core styles
import '@splidejs/react-splide/css/core';
import axios from "axios";
import setting from '../setting.json'

const words = [
    { title: 'Gyz eneden grelde almasa, wt almaz' },
    { title: 'Enesizi gelin edinme, atasyzy giew' },
    { title: 'Gyzyny wenden ga, eesi weni alyp ga' },
    { title: 'Gelene baldyz, gidene ee' },
    { title: 'Gle ddm, ola ddm' },
    { title: 'Gyz gapysy a gapysy, mi geler, biri alar' },
    { title: 'Gelni aagy, opany taagy' },
    { title: 'Gelni gelin edn den er' },
    { title: 'Gayn-gelin gapakly gazan' },
    { title: 'Gayn atam ganarym, dolup durar dulumda, gayn enem gazanym getirip goar anymda' },
    { title: 'Gelinde dil ok, gaynda ynsap' }
]


function RationalWords() {

const [data, setData]=useState([])

const fetchdata = async () =>{
   await axios.get(`${setting.SERVER}/api/greatwords`)
    .then(res=> {setData(res.data)})
    .catch(err=>{console.log(err)})
}    
console.log(data,'ds')
    useEffect(()=>{
        fetchdata()
    },[])
    const options = {
        type: 'loop',
        gap: '1rem',
        autoplay: true,
        pauseOnHover: false,
        resetProgress: false,
       
    };
    return (
        <div className="shadow-lg rounded-xl  bg-[#fff]">
            <Splide options={options}
                aria-labelledby="autoplay-example-heading">
                {data.map((word, i) => (
                    <SplideSlide key={i} className="flex justify-center">
                        <p className="md:text-[1vw] font-[500] text-[3vw]">{word.content}</p>
                    </SplideSlide>
                ))}
            </Splide>
        </div>
    )
}


export default RationalWords;